@media (max-width: 767px) {
  .accordion-button {
    display: block;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    margin: 0;
    padding: 0;
    border: 0;
    width: 40px;
    height: 40px;
    background-color: transparent;
  }
  .accordion-button::before, .accordion-button::after {
    content: "";
    position: absolute;
    background-color: #ffffff;
  }
  .accordion-button--closed::before {
    width: 15px;
    height: 1px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .accordion-button--closed::after {
    width: 15px;
    height: 1px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(90deg);
  }
  .accordion-button--open::before {
    width: 15px;
    height: 1px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .accordion-button--open::after {
    content: none;
  }
}

