    right: 0;
    margin: 0;
    padding: 0;
    border: 0;
    width: 40px;
    height: 40px;
    background-color: transparent;
  }
  [accordion].js-closed::before {
    width: 15px;
    height: 1px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  [accordion].js-closed::after {
    width: 15px;
