.promo {
  background-color: $BlackPearl;
  background-image: url("../img/background.webp");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.promo__inner {
  width: 50%;
  min-width: 870px;
  margin: 0 auto;
  padding: 305px 0 295px;

  @include vp-1023 {
    width: 87%;
    min-width: 670px;
    padding: 180px 0 222px;
  }

  @include vp-767 {
    width: 84%;
    min-width: 270px;
    padding: 150px 0 190px;
  }
}

.promo__title {
  font-family: $ff-title;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: $AthensGray;
  margin: 0 0 30px;

  @include vp-1023 {
    font-size: 64px;
    line-height: 75px;
    margin: 0;
  }

  @include vp-767 {
    font-size: 36px;
    line-height: 42px;
    margin: 0 0 8px;
  }
}

.promo__text {
  font-family: $ff-text;
  font-weight: 400;
  font-size: 22px;
  line-height: 34px;
  color: $AthensGray;
  margin: 0 0 63px;
  width: 70%;

  @include vp-1023 {
    width: 100%;
    margin: 0 0 70px;
  }

  @include vp-767 {
    font-size: 14px;
    line-height: 22px;
    margin: 0 0 44px;
  }
}

.promo__link {
  background-color: $DeepSkyBlue;
  padding: 18px 25px;
  border-radius: 3px;
  margin: 0;

  @include vp-1023 {
    margin-left: 7%;
    padding: 18px 25px;
  }

  @include vp-767 {
    margin: 0;
    display: inline-block;
    text-align: center;
    width: 100%;
    padding: 8px 25px;
  }
}

.promo__link-span {
  font-family: $ff-title;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $AthensGray;

  @include vp-767 {
    font-size: 14px;
    line-height: 16px;
  }

  &--desktop {
    @include vp-767 {
      display: none;
    }
  }

  &--mobile {
    display: none;

    @include vp-767 {
      display: inline-block;
    }
  }
}
